@use 'sass:map';

.screening {

	padding-left: get-spacing();
	padding-right: get-spacing();
	padding: get-spacing();
	padding-top: get-spacing(0.5);

	@media ( max-width: bp(sm) ) {
		padding-left: get-spacing(0.5);
		padding-right: get-spacing(0.5);
		padding: get-spacing(0.5);
		padding-top: get-spacing(0.25);
	}

	h3 {
		font-size: map.get($font-sizes, h4);
	}

	.screening__content {
		background-color: rgba( #000, 0.8);
		padding: get-spacing();
		.wp-block-button__link {
			@extend %button-green;
			color: color(white);
		}

		ul {
			list-style: none;
			margin: get-spacing(0.5) 0;
			padding: 0;

			li {
				margin-bottom: get-spacing(0.25);

				a {
					color: color('white');
					position: relative;
					svg {
						fill: white;
					}
					&::before {
						content: '';
						display: inline-block;
						z-index: 10;
						position: relative;
						top: 0.125rem;
						width: 1rem;
						height: 1rem;
						margin-right: 0.5rem;
						background-size: contain;
						background-image: url('../images/icons/stack.svg');
						background-repeat: no-repeat;
					}
				}
				// &:nth-child(1) {
				// 	a {
				// 		&::before {
				// 			background-image: url('../images/icons/checklist.svg');
				// 		}
				// 	}
				// }
				// &:nth-child(2) {
				// 	a {
				// 		&::before {
				// 			background-image: url('../images/icons/clipboard.svg');
				// 		}
				// 	}
				// }
			}
		}
	}
}
